.logoContainer{
		animation-name:logoSequence;
		animation-duration:8s;			
		animation-fill-mode: both;
		position:absolute;
		overflow:hidden;
}

.myName{
		color:white;
		text-align:center;
		font:25px Calibri Light, Modern, Miriam, Arial;
		text-shadow:5px 5px 10px #ffffff;
		letter-spacing:75px;
		position:relative;
		left:40px;
		top:10px;
}

@keyframes logoSequence{
	from{
		border-top:1px solid black;
		border-bottom:1px solid black;
		background-color:black;
		left:20%;
		top:45%;
		width:60%;
		height:50px;
		opacity:0.0;
	}
	
	50%{
		border-top:2px solid #666666;
		border-bottom:2px solid #666666;
		background-color:black;
		left:0px;
		top:45%;
		width:100%;
		height:54px;
		opacity:1.0;
	}
	
	51%{
		border-top:2px solid #666666;
		border-bottom:2px solid #666666;
		background-color:white;
		left:0px;
		top:45%;
		width:100%;
		height:54px;
		opacity:1.0;
	}
	
	55%{
		border-top:2px solid #666666;
		border-bottom:2px solid #666666;
		background-color:white;
		left:0px;
		top:45%;
		width:100%;
		height:54px;
		opacity:1.0;
	}
	
	60%{
		border-top:1px solid #ffffff;
		border-bottom:1px solid #ffffff;
		background-color:white;
		left:0px;
		top:45%;
		width:100%;
		height:54px;
		opacity:0.0;
	}
	
	61%{
		border-top:1px solid #ffffff;
		border-bottom:1px solid #ffffff;
		background-color:black;
		left:0px;
		top:45%;
		width:100%;
		height:54px;
		opacity:0.0;
	}

	62%{
		border-top:1px solid #111111;
		border-bottom:1px solid #111111;
		background-color:black;
		left:0px;
		top:0px;
		width:100%;
		height:30px;
		opacity:0.0;
	}
	
	to{
		border-top:1px solid #888888;
		border-bottom:1px solid #888888;
		background-color:black;
		left:0px;
		top:0px;
		width:100%;
		height:54px;
		opacity:1.0;
	}
}

.logoImage{
		animation-name:logoImageSequence;
		animation-duration:8s;
		animation-fill-mode: both;		
		position:absolute;
}

@keyframes logoImageSequence{
	from{
		left:0px;
		top:-1000px;
		width:100%;
		height:853px;
		opacity:0.0;
	}
	
	20%{
		left:0px;
		top:-900px;
		width:100%;
		height:853px;
		opacity:0.0;
	}
	
	46%{
		left:0px;
		top:-600px;
		width:100%;
		height:853px;
		opacity:0.4;
	}
	
	50%{
		left:0px;
		top:0px;
		width:100%;
		height:853px;
		opacity:1.0;
	}
	
	55%{
		left:0px;
		top:0px;
		width:100%;
		height:853px;
		opacity:0.0;
	}
	
	to{
		left:0px;
		top:-400px;
		width:100%;
		height:853px;
		opacity:0.6;
	}
}
