.panelPositioner{
	border:0px solid red;
	position:absolute;
	left:50%;
	top:170px;
	width:1px;
	height:1px;
}

.panelContainer{
	border:2px solid white;
	border-radius:25px;
	box-shadow:0px 0px 5px 5px rgba(255, 255, 255, 0.15);
	position:absolute;
	left:-430px;
	top:0px;
	width:860px;
	height:500px;
	overflow:hidden;
}

.initialPanel{
	animation-name:firstPanelSequence;
	animation-duration:8s;
	animation-fill-mode: both;
}

@keyframes firstPanelSequence{
	from{
		opacity:0.0;
	}
	80%{
		opacity:0.0;
		transform:scale(0.95, 0.95);
	}
	to{
		opacity:1.0;
		transform:scale(1.0, 1.0);
	}
}

.panelText{
	padding:20px;
	background:linear-gradient(to bottom, rgba(0,0,0,0.90), rgba(0,0,0,0.4));
	border-radius:20px;
	box-shadow:0px 0px 5px 5px rgba(255, 255, 255, 0.25);
	position:absolute;
	left:10px;
	top:10px;
	width:800px;
	height:440px;
}

.panelHeading{
	text-align:center;
	text-shadow:2px 2px 4px #444444;
	background:linear-gradient(to bottom, rgba(255,255,255,1.0), rgba(255,255,255,0.6));
	border-radius:4px;
	color:black;
	width:100%;
	height:80px;
}

.panelTitle{
	font-size:22px;
	font-weight:bold;
	letter-spacing:15px;
}

.panelSubtitle{
	font-size:15px;
	font-weight:bold;
	letter-spacing:8px;
}

.panelPicture{
	border:1px solid red;
	transform:rotate(45deg);
}

.panelLink{
	color:#ff6666;
}

@keyframes panelBackground_1280x853_Sequence{
	from{
		background-position:-400px -200px;
		background-size:1280px 853px;
	}
	to{
		background-position:0px 0px;
		background-size:860px 500px;
	}
}

.musicPanel{
	animation-name:panelBackground_1280x853_Sequence;
	animation-duration:30s;			
	animation-fill-mode: both;	
	position:absolute;
	/*background-image:url("assets/parrot-2909834_1280.jpg");*/
}
