.navigatorPositioner{
	animation-name:navigatorSequence;
	animation-duration:8s;			
	animation-fill-mode: both;
	
	border:0px solid white;
	position:absolute;
}

@keyframes navigatorSequence{
	from{
		border:0px solid white;
		position:absolute;
		left:50%;
		top:600px;
		width:1px;
		height:1px;
		opacity:0.0;
		transform:scale(0.0, 0.0);
	}
	
	89%{
		transform:scale(0.0, 0.0);
	}
	
	90%{
		border:0px solid white;
		position:absolute;
		left:50%;
		top:600px;
		width:1px;
		height:1px;
		opacity:0.0;
		transform:scale(0.8, 0.8);
	}
	
	to{
		border:0px solid white;
		position:absolute;
		left:50%;
		top:600px;
		width:1px;
		height:1px;
		opacity:1.0;
		transform:scale(1.0, 1.0);
	}
}

.navigatorButton{
	border:2px solid #ffffff;
	background: radial-gradient(#555555 20%, #777777 60%, #222222 70%);
	position:absolute;
	top:0px;
	width:40px;
	height:40px;
	border-radius:1000px;
	cursor:pointer;
}

.navigatorArrow{
	position:absolute;
	left:-5px;
	top:6px;
	width:50px;
	height:50px;
	text-align:center;
	text-shadow:2px 2px 4px #000000;
	color:#ffffff;
	font:25px Lucida Console;
	font-weight:bold;
}

.navigatorPrev{
	left:-65px;
}

.navigatorNext{
	left:15px;
}

@keyframes navigatorClickSequence{
	from{
		opacity:1.0;
	}
	10%{
		opacity:0.7;
		transform:scale(0.95, 0.95);
		background: radial-gradient(#ffffff 20%, #999999 60%, #000000 70%);
	}
	to{
		opacity:1.0;
		background: radial-gradient(#666666 20%, #777777 60%, #000000 70%);
	}
}