.backgroundContainer{
	position:fixed;
	left:0px;
	top:0px;
	width:100%;
	height:100%;
	overflow:hidden;
}

.backgroundImage{
	animation-name:background_1280x853_Sequence;
	animation-duration:20s;			
	animation-fill-mode: both;
	animation-iteration-count:infinite;
	position:absolute;
}

@keyframes background_1280x853_Sequence{
	from{
		left:-150px;
		top:-150px;
		width:2000px;
		height:1300px;
	}
	50%{
		background-position:0px 0px;
		background-size:1700px 1000px;
		left:0px;
		top:0px;
		width:1700px;
		height:1000px;
	}
	to{
		left:-150px;
		top:-150px;
		width:2000px;
		height:1300px;
	}
}

.backgroundPulse{
	animation-name:backgroundPulseSequence;
	animation-duration:15s;	
	animation-delay:4s;
	/*animation-fill-mode:both;*/
	animation-iteration-count:infinite;
	opacity:0.0;
}

@keyframes backgroundPulseSequence{
	from{opacity:0.0;}
	10%{opacity:0.04;}
	20%{opacity:0.06;}
	30%{opacity:0.07;}
	40%{opacity:0.05;}
	50%{opacity:0.04;}
	60%{opacity:0.06;}
	70%{opacity:0.04;}
	80%{opacity:0.08;}
	90%{opacity:0.03;}
	to{opacity:0.0;}
}